@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Open+Sans:wght@300;400;600&display=swap);
:root {
    --black: rgb(47, 48, 51);
    --white: #f8f8f8;
    --gold: rgb(200,165,106);
    --copper: rgb(231, 147, 82);;
  }
  
  * {
    box-sizing: border-box;
    margin: 0;
  }
  
  body {
    font-family: 'Open Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: rgb(47, 48, 51);
    background-color: var(--black);
  }
  
  .App {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    min-height: 100vh;
  }

  
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: var(--gold);
    font-size: 0.8rem;
    padding: 2rem 0 3rem;
  }
  
  .reactLogo {
    font-size: 1.2rem;
    margin-left: 0.2rem;
  }
.heroSection {
    color: var(--white);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 4rem;
    width: 100%;
  }
  
.nameAboutWrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 85%;
}
  
.fullName {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.5rem;
  font-weight: 500;
  padding-top: 4rem;
}
  
.fullName p {
  margin: 0 0 .5rem;
  text-align: left;
}
  
.about {
  font-size: 1rem;
  text-align: center;
  margin: 3.5rem 0 0;
}

.socialWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  padding: 4rem 0 7rem;
}
  
.social {
  font-size: 1.8rem;
  -webkit-flex-basis: 15%;
          flex-basis: 15%;
}

.linkedin,
.github,
.codepen {
  color: var(--white);
  cursor: pointer;
}

.linkedin:hover,
.github:hover,
.codepen:hover {
  color: var(--gold);
}
  
@media (min-width: 375px) {

  .fullName {
    font-size: 3rem;
  }
  
  .about {
    font-size: 1.05rem;
  }

  .social {
    font-size: 2.2rem;
  }

}
  
@media (min-width: 600px) {
  .fullName{
    font-size: 3.6rem;
  }
}
  
@media (min-width: 768px) {

  .heroSection {
    padding-top: 7rem;
  }

  .nameAboutWrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;

  }

  .fullName {
    font-size: 3.6rem;
    padding: 0;
  }

  .about {
    margin: 0;
    width: 17rem;
    padding-bottom: 1rem;
    font-size: 1.2rem;
  }

  .socialWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    padding: 4rem 0 7rem;

  }

  .social {
    -webkit-flex-basis: 4rem;
            flex-basis: 4rem;
  }
}
  
@media (min-width: 1024px) {

  .fullName {
    font-size: 5rem;
  }

  .about {
    width: 20rem;
    font-size: 1.4rem;   
  }

  .social {
    font-size: 2.5rem;
    -webkit-flex-basis: 4.5rem;
            flex-basis: 4.5rem;
  }

  .name:hover,
  .surname:hover,
  .about:hover {
    color: var(--gold);
    cursor: default;
  }

  .footer:hover {
    color: var(--white);
    cursor: default;
  }
}
  
@media (min-width: 1440px) {

  .nameAboutWrapper{
    width: 90%;
    margin-top: 2rem;
  }

  .fullName {
    font-size: 8rem;
  }

  .about {
    width: 23rem;
    font-size: 1.6rem;   
    padding-bottom: 3rem;
  }

  .socialWrapper{
    padding: 4.5rem 0 7rem;
  }

  .social {
    font-size: 2.7rem;
    -webkit-flex-basis: 5rem;
            flex-basis: 5rem;
  }
}
  
@media (min-width: 1877px) {
  
  .nameAboutWrapper{
    width: 70%;

  }
}
.wrapper {
    margin: 12rem 0 10rem;
}

h1 {
    font-size: 2rem;
    color: white;
    font-family: 'Josefin Sans', sans-serif;
}

.icons-container {
    font-size: 3rem;
    color: var(--white);
    margin-bottom: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.icon{
    width: 4rem;
}

.project-card  {
    border: 1px solid var(--gold);
    width: 22rem;
    height: 22rem;
    margin: 1rem 0 1rem;
    color: var(--white);
}

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  top: 0;
  width: 100%;
  position: absolute;
  top: 3rem;
}

.nav-wrapper {
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
}

nav ul{
  padding: 0;  
}

ul li {
  list-style-type: none;
  font-size: .8rem;
  width: 6.2rem;
  color: var(--gold)
}

.nav-wrapper li:hover{
  color: var(--white);
}

ul li ul li {
  padding: 0.7rem 0;
}

ul li ul li:nth-child(1) {
  padding-top: 1.5rem;
}

ul li ul li:last-child {
  padding-bottom: 1.5rem;
}

nav ul li ul {
 
  display: none;
}

nav ul li:hover ul {
  display: block;
  background-color: var(--black);
  border-left: 1px solid var(--gold);
  border-right: 1px solid var(--gold);
  border-bottom: 1px solid var(--gold);
  border-radius: 0 0 5px 5px;
}

.nav-links {
  text-decoration: none;
  color: var(--gold);
}
.nav-links:hover {
  color: var(--white);
}

.nav-links-project {
  text-decoration: none;
  color: var(--white);
}

.nav-links-project:hover {
  color: var(--gold);
}
